import React, { useEffect, useState } from "react";
import "./posts.css";
import axios from "axios";
import FavoriteIcon from "@material-ui/icons/Favorite";
import Skeleton from "@material-ui/lab/Skeleton";

const Posts = (props) => {
    const { userData } = props;
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(true);

    const [like, setLike] = useState(null);
    const postsArr = [];
    for (let i = 0; i < 5; i++) {
        postsArr.push(posts[i]);
    }

    useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await axios(
                    "https://jsonplaceholder.typicode.com/posts"
                );
                setPosts(res.data);
                setLoading(false);
            } catch (err) {
                console.log(err);
            }
        };

        fetchData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <>
            <div className="container">
                {loading || !posts ? (
                    <div className="posts-loading-wrapper">
                        <div>
                            <Skeleton
                                className="mb"
                                animation="wave"
                                variant="circle"
                                width={50}
                                height={50}
                            />
                            <Skeleton
                                className="mb"
                                animation="wave"
                                variant="h1"
                                width={400}
                                height={30}
                            />
                        </div>
                        <Skeleton
                            animation="wave"
                            variant="rect"
                            width={510}
                            height={200}
                            className="mb"
                        />
                        <Skeleton
                            animation="wave"
                            variant="rect"
                            width={510}
                            height={50}
                        />
                    </div>
                ) : (
                    <>
                        <h1>All Posts üòç</h1>
                        {postsArr.map((item) => {
                            return (
                                <>
                                    <div className="post-wrapper" key={item.id}>
                                        <div className="post-header">
                                            <span className="post-header-pp">
                                                <img
                                                    src={userData.picture.large}
                                                    alt=""
                                                />
                                            </span>
                                            <span>
                                                {userData.login.username}
                                            </span>
                                        </div>
                                        <div className="post-body">
                                            <h4>"{item.title}"</h4>
                                            <p>{item.body}</p>
                                        </div>
                                        <div
                                            className="post-footer"
                                            onClick={() =>
                                                setLike(
                                                    item.id === like
                                                        ? null
                                                        : item.id
                                                )
                                            }
                                        >
                                            <div
                                                className={
                                                    item.id === like
                                                        ? "like-btn"
                                                        : "unlike-btn"
                                                }
                                            >
                                                <FavoriteIcon />
                                            </div>
                                        </div>
                                    </div>
                                </>
                            );
                        })}
                    </>
                )}
            </div>
        </>
    );
};

export default Posts;
